.nftBox{
    width: 100%;
    margin-bottom: 50px;
    position: fixed;
    left: 0;
    top: 0;
    padding: 70px 30px 0 30px;
}
.titleBox{
    display: flex;
    justify-content: space-between;
    position: absolute;
    left: 0;
    top: 0;
    height: 380px;
    z-index: -1;
}
.img1{
    width: 30%;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 330px);
}
.img2{
    width: 40%;
    height: 430px;
}
.img3{
    width: 30%;
    clip-path: polygon(0 0, 100% 0, 100% 330px, 0 100%);
}
.nftText{
    padding: 3rem 2rem;
    font-size: 90px;
    font-weight: bold;
}